import React, { useEffect, useState } from "react";
import logo from "./logo.svg";
import "./App.css";

// Initialize deferredPrompt for use later to show browser install prompt.
let deferredPrompt: any;
const iAm = new URLSearchParams(window.location.search).get("name");
function App() {
  const [showPrompt, togglePrompt] = useState(false);
  const [msg, setMsg] = useState("Nobody talk to me");

  useEffect(() => {
    window.addEventListener("beforeinstallprompt", (e: any) => {
      // Prevent the mini-infobar from appearing on mobile
      e.preventDefault();
      // Stash the event so it can be triggered later.
      deferredPrompt = e;
      // Update UI notify the user they can install the PWA
      togglePrompt(true);
      // Optionally, send analytics event that PWA install promo was shown.
      console.log(`'beforeinstallprompt' event was fired.`);
    });

    //listen to messages
    window.addEventListener("message", (event) => {
      if (event.data && event.data.type === `TALK_TO_${iAm}`) {
        //Process message
        setMsg(`Page ${event.data.payload} talk to me`);
      }
    });
  }, []);

  const onClick = () => {
    togglePrompt(false);
    deferredPrompt.prompt();
    // Wait for the user to respond to the prompt
    deferredPrompt.userChoice.then((choiceResult: any) => {
      if (choiceResult.outcome === "accepted") {
        console.log("User accepted the install prompt");
      } else {
        console.log("User dismissed the install prompt");
      }
    });
  };

  const onTalk = (to: string) => {
    window.navigator?.serviceWorker?.controller?.postMessage({
      type: `TALK_TO_${to}`,
      payload: {
        from: iAm,
      },
    });
  };

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <button onClick={onClick}>Install PWA</button>
        {showPrompt && <div>Install!!!</div>}
        <div style={{ color: "#FFFFFF" }}>I am page {iAm}</div>
        <div>
          <button onClick={() => onTalk("A")}>Talk to page a</button>
          <button onClick={() => onTalk("B")}>Talk to page b</button>
          <button onClick={() => onTalk("C")}>Talk to page c</button>
        </div>
        <div>{msg}</div>
      </header>
    </div>
  );
}

export default App;
